/**
 *      .
 * 
 * @param {Array} arr  -.
 * @param {String} key  ,    .
 * @returns {Object}  -,       
 *    key,        key:
 *  [ 'key_value_1': [ {}, {} ], 'key_value_2': [ {}, {} ] ]
 */
export const groupBy = (arr, key) => {
  const result = {};
  arr.forEach(item => {
    if (item[key] in result) {
      result[item[key]].push(item);
    } else {
      result[item[key]] = new Array(item);
    }
  });
  return result;
}
